body {
  font-family: Georgia, 'Times New Roman', Times, serif;
  width: 100%;
}

h1, h2 {
  margin: 0;
  text-align: center;
  font-weight: normal;
}

h1 {
  font-size: 40px;
}

h2 {
  font-size: 32px;
}

h3, h4 {

  font-weight: normal;
  color: #686868;
  margin-bottom: 1rem;
}

h3 {
  margin-top: 2rem;
  font-size: 24px;
}

h4 {
  font-size: 20px;
}

.desktop-heading {
  display: none;
}

.mobile-heading {
  display: block;
  text-align: center;
  margin: 0;
}


p {
  font-size: 16px;
  color: rgb(115,115,115);
  letter-spacing: 0.04px;
  line-height: 24px;
  word-break: break-word;
}

p:last-of-type {
  margin-bottom: 0;
}

main {
  max-width: 650px;
  overflow: hidden;
}

img {
  max-width: 100%;
}

article {
  margin-right: 1rem;
  margin-left: 1rem;
}

label {
  color: #686868;
  font-weight: normal;
}

/* footer */

footer {
  background: #dedede;
  width: 100%;
}

form {
  width: 550px;
  max-width: 100%;
}

textarea {
  height: 200px;
}

 /* map  */
.mapouter {
  overflow:hidden;
  height:380px;
  width:550px;
  max-width: 100%;
}
.gmap_canvas {
  background:none!important;
  height:380px;
  width:550px;
}

#logo {
  padding: 2rem 1rem;
}

/* nav */

ul {
  list-style-type: none;
  display: inline-flex;
  padding-left: 0;
}

a {
  color: black;
}

li a {
  font-size: 18px;
  text-decoration: none;
}

li a:hover {
  text-decoration: underline;
}

.nav-break {
  padding: 0 5px;
  color: lightgray;
}


 /* flex */

.flex {
  display: flex;
}

.flex-column {
  flex-direction: column;
}

.flex-row {
  flex-direction: row;
}

.align-center {
  align-items: center;
}

.align-end {
  align-items: flex-end;
}

.align-start {
  align-items: flex-start;
}

.justify-center {
  justify-content: center;
}

.justify-evenly {
  justify-content: space-evenly;
}

.flex-row-l-col-s {
  flex-direction: column;
}

.flex-row-l-colreverse-s {
  flex-direction: column-reverse;
}

.align-end-l-center-s, 
.align-start-l-center-s {
  align-items: center;
}

/* margins */

.ml-1 {
  margin-left: 1rem;
}

.mr-1 {
  margin-right: 1rem;
}

.ml-2 {
  margin-left: 2rem;
}

.mr-2 {
  margin-right: 2rem;
}

.mt-1 {
  margin-top: 1rem;
}

.mt-2 {
  margin-top: 2rem;
}

.mt-3 {
  margin-top: 3rem;
}

.mt-5 {
  margin-top: 5rem;
}

.mt-2-l {
  margin-top: 1rem;
}

.ml-3 {
  margin-left: 3rem;
}

.mb-2 {
  margin-bottom: 2rem;
}

.mt-s {
  margin-top: 1rem;
}

/* padding  */

.pr-1 {
  padding-right: 1rem;
}

.pr-2 {
  padding-right: 2rem;
}

.pt-2 {
  padding-top: 2rem;
}

.pl-1 {
  padding-left: 1rem;
}

.pl-2 {
  padding-left: 2rem;
}

.pl-3 {
  padding-left: 3rem;
}

.pr-3 {
  padding-right: 3rem;
}

.pt-3 {
  padding-top: 3rem;
}

.pl-5 {
  padding-left: 5rem;
}

.pr-s {
  padding-right: 0;
}

.pl-l {
  padding-left: 0;
}

/* divider images */

.divider {
  height: 125px;
  background-color: #ffffff57;
  background-blend-mode: lighten;
}

#divider-image-1, 
#divider-image-2,
#divider-image-3 {
  position: relative;
}


#divider-image-1::after, 
#divider-image-2::after,
#divider-image-3::after {
  content: '';
  position: absolute;
  left: -2rem;
  height: 125px;
  width: 700px;
  background-color: #ffffff52;
  background-blend-mode: lighten;
}

#divider-image-1::after {
  background-image: url(../img/div-1.jpg);
  background-position: initial;
  background-size: cover;
}


#divider-image-2::after {
  background-image: url(../img/div-2.jpg);
  background-position: initial;
}

#divider-image-3::after {
  background-image: url(../img/div-2.jpg);
  background-position: bottom;
}


/* media queries */

@media screen and (min-width: 850px) {

  main {
      max-width: 800px;
      margin: auto;
  }

  article {
    margin-right: 9rem;
  }

  h1, h2 {
    text-align: left;
  }

  .desktop-heading {
    display: block;
  }

  .mobile-heading {
    display: none;
  }

  img {
    max-width: none;
  }

  .flex-row-l-col-s, 
  .flex-row-l-colreverse-s {
    flex-direction: row;
  }

  .align-end-l-center-s {
    align-items: flex-end;
  }

  .align-start-l-center-s {
    align-items: flex-start;
  }

  .mt-s {
    margin-top: 0;
  }

  .pr-s {
    padding-right: 2rem;
  }

  .pl-l {
    padding-left: 3rem;
  }

  .mt-2-l {
    margin-top: 2rem;
  }

  #divider-image-1::after, 
  #divider-image-2::after, 
  #divider-image-3::after {
    left: 0;
    width: 640px;
  }
}


@media screen and (max-width: 400px) {
  h1 {
    font-size: 34px;
  }

  h2 {
    font-size: 24px;
  }
}